import { renderComponent } from '../react-dom/diff'

const setStateQueue = []; // ÈòüÂàóÊù•‰øùÂ≠òsetSateÊï∞ÊçÆ
const renderQueue = [];


export function enqueueSetState(stateChange, component) {

    // Â¶ÇÊûúsetStateQueueÁöÑÈïøÂ∫¶ÊòØ0Ôºå‰πüÂ∞±ÊòØÂú®‰∏äÊ¨°flushÊâßË°å‰πãÂêéÁ¨¨‰∏ÄÊ¨°ÂæÄÈòüÂàóÈáåÊ∑ªÂä†
    if (setStateQueue.length === 0) {
        defer(flush);
    }

    // Â∞Ü stateChange push Âà∞ setState ÈòüÂàó
    setStateQueue.push({
        stateChange,
        component
    });

    // Â¶ÇÊûúrenderÈòüÂàóÂΩìÂâçÊ≤°ÊúâÊ≠§component push ËøõÂéª
    if (!renderQueue.some(item => item === component)) {
        renderQueue.push(component);
    }
}

function flush() {
    // ÈÅçÂéÜ setStateÊìç‰Ωú
    while (setStateQueue.length) {
        const { stateChange, component } = setStateQueue.shift();
        if (!component.prevState) {
            component.prevState = Object.assign({}, component.state);
        }
        // Â¶ÇÊûústateChangeÊòØ‰∏Ä‰∏™ÊñπÊ≥ï
        // ‰æãÂ¶ÇÔºöthis.setState((state, props) => ({ counter: state.counter + props.increment }));
        if (typeof stateChange === 'function') {
            Object.assign(component.state, stateChange(component.prevState, component.props));
        } else {
            // Â¶ÇÊûústateChangeÊòØ‰∏Ä‰∏™ÂØπË±°ÔºåÂàôÁõ¥Êé•ÂêàÂπ∂Âà∞setState‰∏≠
            Object.assign(component.state, stateChange);
        }
        component.prevState = component.state;
    }
    //setStateÂêàÂπ∂‰ª•ÂêéÔºåËøõË°åÊ∏≤Êüì
    while (renderQueue.length) {
        const component = renderQueue.shift();
        renderComponent(component);
    }
}

// ËÆ© flush Âú®ÊâÄÊúâÂêåÊ≠•‰ª£Á†ÅÁªìÊùüÂêéÊâßË°å
function defer(fn) {
    return Promise.resolve().then(fn);
}