
import { diff } from './diff'

/**
 * 
 * vNodeÊúâ‰∏âÁßçÁªìÊûÑÔºö
 * 1.ÂéüÁîüDOMËäÇÁÇπÁöÑvnode
 * {
 *     tag: 'div',
 *     attrs: {
 *         className: 'container'
 *     },
 *     children: []
 * }
 * 
 * 2.ÊñáÊú¨ËäÇÁÇπÁöÑvnode
 * "hello,world"
 * 
 * 3.ÁªÑ‰ª∂ÁöÑvnode
 * {
 *     tag: ComponentConstrucotr,
 *     attrs: {
 *         className: 'container'
 *     },
 *     children: []
 * }
 */
function _render(vnode) {
    if (vnode === undefined || vnode === null || typeof vnode === 'boolean') vnode = '';
    if (typeof vnode === 'number' || vnode instanceof Date) vnode = String(vnode);
    // Â¶ÇÊûúÊòØÁ∫ØÊñáÊú¨
    if (typeof vnode === 'string') {
        let textNode = document.createTextNode(vnode);
        return container.appendChild(textNode);
    }
    // Â¶ÇÊûú vnode ÊòØÁªÑ‰ª∂Êó∂ÁöÑÂ§ÑÁêÜ,ÁªÑ‰ª∂ÁöÑtagÊòØ‰∏Ä‰∏™ÊûÑÈÄ†ÂáΩÊï∞
    if (typeof vnode.tag === 'function') {
        // const component = createComponent(vnode.tag, vnode.attrs);
        // setComponentProps(component, vnode.attrs);
        // return component.base;
        const component = vnode;
        if (component._container) {
            if (component.componentWillUpdate) {
                component.componentWillUpdate();
            }
        } else if (component.componentWillMount) {
            component.componentWillMount();
        }
        component._container = container;   // ‰øùÂ≠òÁà∂ÂÆπÂô®‰ø°ÊÅØÔºåÁî®‰∫éÊõ¥Êñ∞
        vnode = component.render();
    }
    const dom = document.createElement(vnode.tag);
    // ÊåÇËΩΩÂ±ûÊÄß
    if (vnode.attrs) {
        Object.keys(vnode.attrs).forEach(key => {
            const value = vnode.attrs[key];
            if (key === 'className') key = 'class';
            // Â¶ÇÊûúÊòØ‰∫ã‰ª∂ÁõëÂê¨ÂáΩÊï∞ÔºåÂàôÁõ¥Êé•ÈôÑÂä†Âà∞dom‰∏ä
            if (typeof value === 'function') {
                dom[key.toLowerCase()] = value;
            } else {
                dom.setAttribute(key, vnode.attrs[key]);
            }
        });
    }
    // ÈÄíÂΩíÂ≠êÂÖÉÁ¥†
    vnode.children && vnode.children.forEach(child => _render(child, dom));    // ÈÄíÂΩíÊ∏≤ÊüìÂ≠êËäÇÁÇπ
    return container.appendChild(dom);
}

/**
 * React.render()
 * renderÊñπÊ≥ïÁöÑ‰ΩúÁî®ÊòØÂ∞ÜËôöÊãüDOMÊ∏≤ÊüìÊàêÁúüÂÆûÁöÑDOM
 */
function render(vnode, container, dom) {
    console.log(container, vnode, container);
    return diff(dom, vnode, container);
}

export default render